<template>
  <div class="love-oath">
    <Banner></Banner>

    <div class="row my-oath issue-an-oath">
      <h2>发布誓言</h2>
      <form @submit="onSubmit" @reset="onReset">
        <div class="thumbnail">
          <div class="con4">
            <canvas id="cvs" width="220" height="220"></canvas>
            <span class="btn upload">上传图片<input type="file" class="upload_pic" id="upload" v-on:change="getFile" required></span>
            <div class="prompt">建议图片大小：300*300像素</div>
          </div>
        </div>
        <div class="caption">
          <div class="form-group">
            <label for="exampleInputEmail1">爱情誓言主题*</label>
            <input type="text" class="form-control" v-model="form.title" maxlength="30" placeholder="最多可输入30字" required>
          </div>
          <div class="form-group">
            <label for="exampleInputEmail1">爱情誓言内容*</label>
            <textarea class="form-control" rows="3" v-model="form.message" maxlength="300" placeholder="最多可输入300字" required></textarea>
          </div>
          <div class="form-group">
            <button type="reset" class="btn">重置</button>
            <button type="submit" class="btn btn-default">发布誓言</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script >
import api from '../libs/api'
import image from '../libs/image'
import Banner from "./Banner";

export default {
  name: 'CreateLove',
  components: {Banner},
  data () {
    return {
      form: {
        title: '',
        message: '',
        file: ''
      }
    }
  },
  created () {
    setTimeout("window.scrollTo(0,800)",2000)
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      let self = this
      image.PhotoCompress(this.form.file, {
        quality: 0.8,
        width: 320,
      }, re => {
        api.publishLove(this.form.title, this.form.message, re, resp => {
          self.$router.push({path: '/'})
        })
      })
    },
    onReset (evt) {
      evt.preventDefault()
      this.form.title = ''
      this.form.message = ''
      this.form.file = ''
    },
    getFile($event){
      this.form.file = $event.target.files[0] //获取要上传的文件
      if(!/image\/\w+/.test(this.form.file.type)){
        //图片文件的type值为image/png或image/jpg
        alert("文件必须为图片！");
        return false;
      }
      // console.log(file);
      var reader = new FileReader();//实例一个文件对象
      reader.readAsDataURL(this.form.file);//把上传的文件转换成url
      //当文件读取成功便可以调取上传的接口
      reader.onload = function(e){

        var image = new Image();
        // 设置src属性
        image.src = e.target.result;
        var max=200;
        // 绑定load事件处理器，加载完成后执行，避免同步问题
        image.onload = function(){
          // 获取 canvas DOM 对象
          var canvas = document.getElementById("cvs");
          // 如果高度超标 宽度等比例缩放 *=
          /*if(image.height > max) {
                    image.width *= max / image.height;
                    image.height = max;
                } */
          // 获取 canvas的 2d 环境对象,
          var ctx = canvas.getContext("2d");
          // canvas清屏
          ctx.clearRect(0, 0, canvas.width, canvas.height);
          // 重置canvas宽高
          /*canvas.width = image.width;
                canvas.height = image.height;
                if (canvas.width>max) {canvas.width = max;}*/
          // 将图像绘制到canvas上
          // ctx.drawImage(image, 0, 0, image.width, image.height);
          ctx.drawImage(image, 0, 0, 220, 220);
          // 注意，此时image没有加入到dom之中
        };
      }
    }
  }
}
</script>



