<template>
  <div class="love-oath">
    <Banner></Banner>
    <div class="row">
      <h2>爱情誓言墙</h2>
      <div class="row_box" v-for="loves in toDyadic(loveList,3)" :key="Math.random()">
        <div class="list" v-for="love in loves" v-if="love !== undefined" :key="Math.random()">
          <div class="thumbnail">
            <img :src="love.image">
          </div>
          <div class="caption">
            <h3>{{love.title}}</h3>
            <p v-for="messagee in love.message.split('\n')" :key="messagee.message" style="height: auto;overflow:inherit;text-align:left;text-indent:2em" >
              {{messagee}}
            </p>
            <img src="../../static/img/comma01.png" class="comma_top">
            <img src="../../static/img/comma02.png" class="comma_bottom">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../libs/api'
import Banner from "./Banner";
import Load from "./Load";

export default {
  name: 'Index',
  components: {Load, Banner},
  data () {
    return {
      loadShow:true,
      loveList: [],
      offset: 0,
      pageNum:99
    }
  },
  created () {
    api.getByDesc(this.offset, this.pageNum, resp => {
      this.loveList = this.loveList.concat(JSON.parse(resp.result))
      this.offset = this.loveList.length
      setTimeout("window.scrollTo(0,800)", 2000)
      setTimeout("autoHeight()", 500)
      this.loadShow = false
      //this.more()
    })
  },
  methods: {
    more () {
      api.getByDesc(this.offset, this.pageNum, resp => {
        this.loveList = this.loveList.concat(JSON.parse(resp.result))
        this.offset = this.loveList.length
        setTimeout("autoHeight()",500)
         // if(JSON.parse(resp.result).length != 0){
         //   this.more();
         // }
      })
    },
    sub (message) {
      if (message.length > 100) {
        return message.substring(0, 100) + '...'
      } else {
        return message
      }
    },
    toDyadic (arr,subArrayNum){
      var Arr = new Array();
      for(var i = 0; i<arr.length;i++){
        var s = Math.floor(i/subArrayNum);
        if(Arr[s] === undefined){
          Arr[s] = new Array()
        }
        Arr[s][i%subArrayNum] = arr[i];
      }
      return Arr;
    }
  }
}
</script>



