import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import CreateLove from '@/components/CreateLove'
import MyLove from '@/components/MyLove'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '/createLove',
      name: 'CreateLove',
      component: CreateLove
    },
    {
      path: '/myLove',
      name: 'MyLove',
      component: MyLove
    }
  ]
})



